/* ############## Mause CSS in Welcome Section ################# */
.mause{
    position: absolute;
    border: 2px solid crimson;
    width: 32px;
    height: 45px;
    border-radius: 30px;
    bottom: 100px;
    left: 230px;
}
.wheele{
    position: absolute;
    width: 4px;
    height: 15px;
    background-color: crimson;
    border-radius: 4px;
    left: 12px;
    top: 10px;
    -webkit-animation: wheele 1.3s linear infinite;
            animation: wheele 1.3s linear infinite;
}
@-webkit-keyframes wheele{
    0%{
        top: 10px;
    }
    50%{
        top: 20px;
    }
    100%{
        top: 10px;
    }
}
